/**
 * 最外层包裹的home组件，包括
 */
import React, { PureComponent } from 'react';
import BasicLayout from 'Layouts/SparepartsManage';
import {
  Route, Redirect,
} from 'react-router-dom';
import {
  SpareDefinePage, StockPage, StockWarning
} from './configs'

class SparepartsRouter extends PureComponent {
  render() {
    return <BasicLayout children={<React.Fragment>
      <Route exact path="/spareparts" render={() => <Redirect to='/spareparts/define' />} />
      <Route path='/spareparts/define' component={SpareDefinePage} />
      <Route path='/spareparts/stock' component={StockPage} />
      <Route path='/spareparts/warning' component={StockWarning} />
    </React.Fragment>} />;
  }

}
export default SparepartsRouter;